import React, { Component } from "react";
import { Nav, NavItem, NavLink, TabContent, TabPane } from "reactstrap";

type Items = Array<string>;

interface Props {
  items: Items;
  heading: string;
  children(props: any): JSX.Element;
}

interface State {
  activeTab: number;
}

export default class Tab extends Component<Props, State> {
  state = {
    activeTab: 1
  };

  private toggle = (id: number) => {
    this.setState({ activeTab: id });
  };

  renderTabHeader = () => {
    const { items } = this.props;
    return items.map((item, index) => {
      return (
        <NavItem>
          <NavLink
            className={this.state.activeTab === index + 1 ? "active" : ""}
            onClick={() => this.toggle(index + 1)}
          >
            {item}
          </NavLink>
        </NavItem>
      );
    });
  };

  render() {
    const { activeTab } = this.state;
    return (
      <React.Fragment>
        <Nav tabs>{this.renderTabHeader()}</Nav>
        <TabContent activeTab={activeTab} className="mt-4">
          <h4>{this.props.heading}</h4>
          <TabPane tabId={activeTab}>{this.props.children(activeTab)}</TabPane>
        </TabContent>
      </React.Fragment>
    );
  }
}
