import React, { Component } from "react";
import { ListGroup, ListGroupItem, Input, Collapse } from "reactstrap";
import { FaCaretDown } from "react-icons/fa";

interface Props {
  items: Array<string>;
  title: string;
}
interface State {
  isOpen: boolean;
}

export default class CheckList extends Component<Props, State> {
  state = {
    isOpen: window.innerWidth > 992
  };

  private updateList = () => {
    const { innerWidth } = window;
    this.setState({ isOpen: innerWidth > 992 });
  };

  componentDidMount() {
    window.addEventListener("resize", this.updateList);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateList);
  }

  private renderListItems = () => {
    const { items } = this.props;
    return items.map(item => (
      <ListGroupItem>
        <div className="pl-2">
          <Input type="checkbox" />
          <span className="text-primary text-uppercase">{item}</span>
        </div>
      </ListGroupItem>
    ));
  };

  private toggle = () => {
    this.setState(({ isOpen }) => ({ isOpen: !isOpen }));
  };

  render() {
    const { title } = this.props;
    return (
      <ListGroup>
        <ListGroupItem className="bg-primary text-white">
          <div className="d-flex justify-content-between align-items-center">
            <span className="text-uppercase">{title}</span>
            <FaCaretDown onClick={this.toggle} className="d-lg-none d-block" />
          </div>
        </ListGroupItem>
        <Collapse isOpen={this.state.isOpen}>{this.renderListItems()}</Collapse>
      </ListGroup>
    );
  }
}
