import React, { Component } from "react";
import { FaRegHeart, FaPhone } from "react-icons/fa";
import { Row, Col, Button, UncontrolledTooltip } from "reactstrap";

interface Props {
  id: string;
  name: string;
  address: string;
  status: boolean;
}
interface State {}

export default class Restaurant extends Component<Props, State> {
  render() {
    const { id, name, address, status } = this.props;
    return (
      <Row className="border-top border-bottom pt-4 pb-4">
        <Col xs="8" lg="4">
          <h5>{name}</h5>
          <span>{address}</span>
        </Col>
        <Col xs="4" lg="2">
          <Button className="btn btn-dark" id={id}>
            {status ? "OPEN" : "CLOSED"}
          </Button>
          <UncontrolledTooltip target={id}>
            {status ? "Closes in " : "Opens at "}
          </UncontrolledTooltip>
        </Col>
        <Col xs="12" lg="6" className="mt-lg-0 mt-2">
          <Button outline color="primary" className="mt-lg-0 mt-2 mr-2">
            <FaRegHeart className="text-primary" />
          </Button>
          <Button outline color="primary" className="mt-lg-0 mt-2 mr-2">
            <FaPhone className="text-primary" />
          </Button>
          <Button outline color="primary" className="mt-lg-0 mt-2 mr-2">
            Map
          </Button>
          <Button color="primary" className="mt-lg-0 mt-2 mr-2">
            Menus
          </Button>
        </Col>
      </Row>
    );
  }
}
