import React, { Component } from "react";
import { Row, Col } from "reactstrap";
import Restaurant from "./Restaurant";
interface Props {
  category: string;
  items: Array<{ name: string; address: string; status: boolean }>;
}
interface State {}
export default class RestaurantList extends Component<Props, State> {
  renderRows = () => {
    const { items } = this.props;
    return items.map(({ name, address, status }, index) => (
      <Restaurant
        id={`row-${index + 1}`}
        name={name}
        address={address}
        status={status}
      />
    ));
  };
  render() {
    const { category } = this.props;
    return (
      <React.Fragment>
        <Row>
          <Col>
            <h4 className="text-primary mb-3 mt-3">{category}</h4>
          </Col>
        </Row>
        {this.renderRows()}
      </React.Fragment>
    );
  }
}
